.App {
  font-family: sans-serif;
}

* {
  margin: 0;
  padding: 0;
}
img,
svg {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
body {
  text-align: left;
  background: #ffffff;
  color: #000000;
  margin: 0;
}
.container {
  margin-left: 20px;
  margin-right: 20px;
}

input,
select,
textarea {
  display: block;
  width: 100%;
  height: 50px;
  line-height: 48px;
  padding: 0 1.5rem;
  background-color: #ffffff;
  border-width: 1px;
  border-style: solid;
  border-color: #dddddd;
  border-radius: 5px;
  outline: 0;
  font-size: 1rem;
  font-weight: 300;
  font-family: "Nunito", sans-serif;
  text-transform: none;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
select {
  cursor: pointer;
  -webkit-appearance: menulist;
}

.overlay.active {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  position: absolute;
  z-index: 10;
}

.button {
  display: inline-block;
  position: relative;
  padding: 0 2.25rem;
  line-height: 45px;
  color: #ffffff;
  background: #00a1ff;
  font-size: 1rem;
  font-weight: 400;
  font-family: "Jost", sans-serif;
  text-transform: none;
  border-width: 0;
  border-style: solid;
  border-color: transparent;
  border-radius: 5px;
  box-shadow: none;
  overflow: visible;
  text-align: center;
  margin: 0;
  outline: 0;
  cursor: pointer;
  white-space: nowrap;
  text-shadow: none;
  backface-visibility: hidden;
  transform: translateZ(0);
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  transition: all 0.3s ease;
}
.button:hover,
.button.active {
  color: #ffffff;
  background: #202020;
  box-shadow: none;
}

/*** Скрол всего сайта ***/
::-webkit-scrollbar {
  width: 5px;
  height: 0;
}
::-webkit-scrollbar-thumb {
  border-radius: 0;
  background-color: #00a1ff;
}
::-webkit-scrollbar-track {
  border-radius: 0;
  background-color: #dddddd;
}
.fancybox-bg {
  background: #1e1e1e;
  opacity: 0;
  transition-duration: inherit;
  transition-property: opacity;
  transition-timing-function: cubic-bezier(0.47, 0, 0.74, 0.71);
}
.fancybox-is-open .fancybox-bg {
  opacity: 0.7;
  transition-timing-function: cubic-bezier(0.22, 0.61, 0.36, 1);
}
.fancybox-bg,
.fancybox-inner,
.fancybox-outer,
.fancybox-stage {
  bottom: 0;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
}
header {
  -webkit-box-shadow: 0px 6px 8px 0px rgba(34, 60, 80, 0.2);
  -moz-box-shadow: 0px 6px 8px 0px rgba(34, 60, 80, 0.2);
  box-shadow: 0px 6px 8px 0px rgba(34, 60, 80, 0.2);
  border-radius: 5px;
  background: #00a1ff;
  padding: 20px 0;
  color: #ffffff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  max-width: 500px;
}
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/*Гео определение города*/
header .locator {
  display: block;
  margin-right: 16px;
  fill: #ffffff;
  width: 16px;
  height: 16px;
}
#geoAreas .geo_block:hover .locator {
  fill: #dddddd;
}
.SearchBlockContainer {
  z-index: 20;
  display: none;
  background: #ffffff;
  border-radius: 5px;
  padding-bottom: 20px;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  left: 50%;
}
.SearchBlockContainer.active {
  display: block;
}
.SearchBlockContainer .SearchBlockUP {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  padding: 20px;
}
.SearchBlockUP .saveCity {
  margin-left: 20px;
}
#SearchCityBlock {
  display: flex;
  padding: 0 20px;
}
#SearchCityBlock .searchTitle {
  font-size: 20px;
  cursor: default;
  color: #afaaaa;
  padding: 10px;
  border-radius: 5px;
  margin-bottom: 10px;
  display: block;
}
#SearchCityBlock .left {
  margin-right: 20px;
}
#SearchCityBlock #region ul,
#SearchCityBlock #city ul {
  height: 340px;
  overflow-y: overlay;
}
#city ul li.active,
#region ul li.active {
  color: #ffffff !important;
  background: #00a1ff;
}
#SearchCityBlock #region ul li:hover,
#SearchCityBlock #city ul li:hover {
  color: #00a1ff;
}
#SearchCityBlock #region ul li,
#SearchCityBlock #city ul li {
  width: 250px;
  white-space: normal;
  cursor: pointer;
  line-height: 30px;
  font-size: 18px;
  list-style-type: none;
  padding: 0 10px;
}
.NameCityBlock,
.SearchBlockContainer .SearchBlockUP {
  padding: 0 20px;
}
.NameCityBlock {
  font-size: 20px;
  cursor: default;
  padding: 20px;
  background: #00a1ff;
  margin-bottom: 20px;
  color: #ffffff;
}
#geoAreas .geo_block {
  cursor: pointer;
  display: flex;
  align-items: center;
}
#del_cookie {
  cursor: pointer;
}
#del_cookie:hover {
  color: #e5e4e6;
}

#geoAreas .geo_block span:first-child {
  cursor: default;
}
#geoAreas .nameCity {
  color: #ffffff;
  cursor: pointer;
}
#geoAreas .geo_block:hover .nameCity {
  color: #e5e4e6;
}
