@media all and (max-width: 480px) {
  /* .cookie {
    top: 30%;
    transform: translate(-50%, -30%);
  } */
  .SearchBlockContainer {
    width: 80%;
  }
  header {
    width: 90%;
  }
  .SearchBlockContainer .SearchBlockUP {
    margin-left: 0;
    margin-bottom: 0;
  }
  #SearchCityBlock {
    display: none;
  }
  .NameCityBlock {
    font-size: 18px;
    margin-left: 0;
  }
  #SearchCityBlock #region ul li,
  #SearchCityBlock #city ul li {
    width: auto;
    margin-right: 15px;
  }
  #SearchCityBlock {
    flex-direction: column;
  }
  #SearchCityBlock #region ul,
  #SearchCityBlock #city ul {
    height: 150px;
  }
  #SearchCityBlock .left {
    margin-right: 0;
    margin-bottom: 20px;
  }
  .SearchBlockContainer .SearchBlockUP {
    flex-direction: column;
  }
  .SearchBlockUP .saveCity {
    margin-left: 0;
    margin-top: 20px;
  }
}
@media all and (max-width: 380px) {
  div#geoAreas {
    margin-bottom: 20px;
  }
  .header {
    flex-direction: column;
  }
}
